.banner{
    height: 448px;
    position: relative;
    object-fit: contain;
    color: white;
}

.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_describtion{
    width: 45rem;
    line-height: 1.3rem;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

.banner--fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,1),
        #111);
}

.banner_button{
    cursor: pointer;
    color: #fff;
    border: none;
    outline: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem;
   background-color: rgba(51,51,51,0.5);
    margin-right: 1rem;
}

.banner_button:hover{
    background-color: #fff;
    transition: ease-in 0.2;
    color: #111;
}
